<script>
	import {
		log,
		promiseHandle
	} from './utils/util';
	import {
		GetjudgeExpire,
		getSessionId,
		checkSessionId,
		isRegistedByWx,
		getEveryDaySign
	} from './utils/api/api';

	export default {
		globalData: {
			userInfo: null,
			sessionId: '',
			registerParam: null, //用户未绑定user，访问wxlogin的参数
			loginFlag: false,
			authorModal: false
		},
		//自定义配置
		settings: {
			debug: true, //是否调试模式
		},
		data() {
			return {}
		},
		onLaunch: function() {
			var self = this;
			//从缓存中读取sessionid
			// uni.clearStorageSync();
			var token = uni.getStorageSync('token'); //sessionAndUuid由sessionId和userId组成
			if (token) {
				self.checkSessionEffect(token);
			} else {
				// this.getUserInfo()
				// uni.redirectTo({url: '/pages/login/wxlogin/wxlogin'});
			}
		},
		onShow: function() {
			console.log("刚启动，看一下token")
			uni.getStorage({
				key:"token",
				success(res) {
					console.log("刚启动，获得了token为",res.data)
					var userid = res.data
					uni.request({
						url: getEveryDaySign(userid) ,
						success(res) {
							console.log("当前用户已经签到",res)
						}
					})
				},
				fail() {
					console.log("启动获得的token失败")
					return ;
				}
			})
			
			// console.log('App Show')
			// uni.login({
			// 	provider: 'univerify',
			// 		univerifyStyle: { 
			// 	    fullScreen: true
			// 	  }
			// })
		},
		onHide: function() {
			// console.log('App Hide')
		},
		methods: {
			getUserInfo: function(e) {
			   let that = this;
			   // 获取用户信息
			   wx.getSetting({
			     success(res) {
			       // console.log("res", res)
			       if (res.authSetting['scope.userInfo']) {
			         // 已经授权，可以直接调用 getUserInfo 获取头像昵称
			         wx.getUserInfo({
			           success(res) {						  
					  wx.getUserProfile({
						  success(res){
							  console.log(res)
						  },
						  fail(res){
							  console.log(res)
							  that.showSettingToast("请授权")

						  }
					  })
			           },
			           fail(res) {
			             console.log("获取用户信息失败", res)
			           }
			         })
			       } else {
			         that.showSettingToast("请授权")
			       }
			     }
			   })
			 },

			// 打开权限设置页提示框
			showSettingToast: function(e) {
				wx.showModal({
					title: '提示！',
					confirmText: '去设置',
					showCancel: false,
					content: e,
					success: function(res) {
						if (res.confirm) {
							uni.navigateTo({
								url: '/pages/auth/auth',
							})
						}
					}
				})
			},
			/**
			 * 获取微信用户的详细信息，包括头像，昵称，城市...
			 * @param cb 回调函数 
			 */
			getwxUserInfo(cb) {
				if (typeof cb !== "function") return;
				let that = this;
				if (that.globalData.userInfo) {
					cb(that.globalData.userInfo);
				} else {
					promiseHandle(uni.login).then(() => promiseHandle(uni.getUserInfo)).then(res => {
						that.globalData.userInfo = res.userInfo;
						cb(res);
					}).catch(err => {
						log(err);
					});
				}
			},
			/**
			 * 检查缓存中的sessionid的值是否过期
			 * @param sessionid_local 本地sessionId
			 * @param successCb 成功的回调，服务器redis不存在改sessionId
			 * @param failCb 失败的回调
			 */
			checkSessionEffect: function(token) {
				var self = this;
			 if (token) {
					uni.request({
						url: GetjudgeExpire(token),
						method: 'GET',
						success(res) {
							if (res.data.code == 200)
								console.log('已登录，token有效');
							else {
								console.log("judgeExpire请求但是缓存过期了", res)
								self.authorModal = false;
								wx.hideTabBar({
									animation: true
								})
								return;
							}
							}
					});
				} else {
					self.doLogin();
				}
			},
			
		}

	}
</script>

